import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faHeart } from '@fortawesome/free-solid-svg-icons'
import { faGithub } from "@fortawesome/free-brands-svg-icons"
import React from "react"
import {Link} from "react-router-dom"

import teamLogo from "../images/team-logo.png"

const Footer = () => {

    return (
        <footer className="footer">
            <p>
                Made with 
                <FontAwesomeIcon icon={faHeart} style={{color: "red", paddingLeft: "7px", paddingRight: "7px"}} />
                 by TransformToConform
                 <a target="_blank" rel="noopener noreferrer" href="https://github.com/quan-xue/transform-conform" >
                    <FontAwesomeIcon icon={faGithub} style={{color: "black", paddingLeft: "7px", paddingRight: "7px"}} />
                 </a>
                 
            </p>
        </footer>
    )
}

export default Footer